<template>
  <div>
<nav-bar :name="title"></nav-bar>
        <v-container class="text-center">
            <h4> INFORMATION REQUIRED FROM THE CUSTOMER </h4>
        </v-container>
        <v-container class="text-center">
            <v-card class="ma-auto px-4" width="90%" outlined>
              <br>
              <v-autocomplete
                outlined
                dense
                placeholder="STORES"
                class="mx-4 mb-n6"
                v-model="selected"
                chips
                :items="items"
                search-input="items"
                multiple
              >
                <template v-slot:prepend-item>
                  <v-list-item
                    ripple
                    @click="toggle"
                  >
                    <v-list-item-action>
                      <v-icon :color="selected.length > 0 ? '#E49711' : ''">
                        {{ icon }}
                      </v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title>
                        Select All
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider class="mt-2"></v-divider>
                </template>
              </v-autocomplete>
                
                <v-layout>
                    <v-col
                    cols="12"
                    >
                    <v-checkbox v-for="(item,id) in options"
                    :key="id"
                    :label="item.label"
                    :value="item.value"
                    v-model="cus.message"
                    color="#E49711"
                    ></v-checkbox>
                    <v-layout align="center">
                      <v-checkbox
                        v-model="enabled"
                        hide-details
                        class="shrink mr-2 mt-0"
                      ></v-checkbox>
                      <v-text-field
                        :disabled="!enabled"
                        label="Other"
                        v-model="other"
                      ></v-text-field>
                    </v-layout>
                    </v-col>
                </v-layout>

                <v-layout>
                    <v-col>
                    <v-btn
                    class="mx-4 my-2"
                    color="#989898"
                    dark
                    @click="$router.go(-1)"
                    >
                    GO BACK
                    </v-btn>
                    <v-btn
                    class="mx-4 my-2 primary"
                    light
                    :loading="loading"
                    @click="send"
                    >
                    SUBMIT REQUEST
                    </v-btn>
                    </v-col>
                </v-layout>
            </v-card>
        </v-container>
  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import StoreService from '@/services/StoreService';
  import Swal from 'sweetalert2';

export default {
    components: {
      NavBar
    },

    data: () => ({
      title: 'CUSTOMER SERVICE',
      loading: false,
      enabled:false,
      other: "",
      items: [

      ],
      selected: [],
      cus:{
        emails: [],
        message: [],
      },
      options: [
        {
          label: "Walmart login.",
          value: "Walmart login"
        },
        {
          label: "Walmart password.",
          value: "Walmart password"
        },
        {
          label: "Return label.",
          value: "Return label"
        },
        {
          label: "Last 4 digits credit card.",
          value: "Last 4 digits credit card"
        },
        {
          label: "Expiration.",
          value: "Expiration"
        },
        {
          label: "CVV.",
          value: "CVV"
        },
        {
          label: "Onboarding.",
          value: "Onboarding"
        },
        {
          label: "Tax exempt wm.",
          value: "Tax exempt wm"
        },
        {
          label: "Permissions.",
          value: "Permissions"
        },
        {
          label: "Full access.",
          value: "Full access"
        },
        {
          label: "Suspension permissions.",
          value: "Suspension permissions"
        },
        {
          label: "Change store name.",
          value: "Change store name"
        },
        {
          label: "Update credit card.",
          value: "Update credit card"
        },
      ],
    }),

    computed: {
      likesAll () {
        return this.selected.length === this.items.length
      },

      likesSome () {
        return this.selected.length > 0 && !this.likesAll
      },
      
      icon () {
        if (this.likesAll) return 'mdi-close-box'
        if (this.likesSome) return 'mdi-minus-box'
        return 'mdi-checkbox-blank-outline'
      },

      data_emails () {
        if(this.selected.length > 1){
          let email = [];
          this.selected.forEach(item => {
            email.push( item.value )
          })
          return email;
        }
        else {
          return this.selected;
        }
      },
    },

    mounted() {
      StoreService
      .list_admin()
      .then(res => {
        for (let i = 0; i < res.length; i++) {
            let store = res[i];
            this.items.push({ text: store.name, value: store.email })
          }
      })
      .catch(e => {
        let error = e.response.data;
        let msg = ''
        Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
        });
            this.loading = false;
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
        })
    },

    methods: {
      toggle () {
        this.$nextTick(() => {
          if (this.likesAll) {
            this.selected = []
          } else {
            this.selected = this.items.slice()
          }
        })
      },

      send(){
        this.loading = true;
        this.cus.emails = this.data_emails;
        if(this.enabled)
          this.cus.message.push(this.other)
        StoreService
          .send_request_info(this.cus)
          .then(res => {
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: res['message']
            }).then(() => {
                this.loading = false
                this.$router.go(-1)
              })
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
      }
    },
}
</script>